$card-indent: $gutter * 2;

.card {
	margin-bottom: $card-indent;

	@include respond-to(md) {
		margin-bottom: $card-indent/2;
	}

	&__top-line {
		margin-bottom: $gutter;
		padding-top: 20px;
		@extend %flexCenter;

		@include respond-to(xs) {
			margin: $gutter/2 0;
			padding-top: 0;
		}
	} //.card__top-line

	&__wrap {
		margin-bottom: $card-indent * 2;
		display: flex;

		@include respond-to(xs) {
			margin-bottom: $card-indent * 1.5;
			flex-wrap: wrap;
		}
	} //.card__wrap

	&__image {
		width: 40%;
		margin-right: $gutter * 3;
		position: relative;

		@include respond-to(sm) {
			width: calc(50% - 40px);
			margin-right: $gutter * 2;
		}

		@include respond-to(xs) {
			width: 100%;
			margin-right: 0;
			margin-bottom: $card-indent/2;
		}
	} //.card__image

	&__badge {
		position: absolute;
		top: 16px;
		right: -16px;
		z-index: 2;
		@include respond-to(xs) {
			top: auto;
			bottom: -4px;
			left: 50%;
			right: auto;
			@include trfX(-50%);
			flex-direction: row;
			.badge {
				& + .badge {
					margin-top: 0;
					margin-left: 10px;
				}
			}
		}
	} //.card__badge

	&__slider {
		.slick-slide {
			margin: 0;
		}

		.slick-dots {
			bottom: -20px;
			li {
				button {
					margin: 0 2px;
					width: 6px;
					height: 6px;
					padding: 0;
					background-color: lighten($gray, 15%);
					border-radius: 50%;

					&::before {
						display: none;
					}

					&:hover {
						width: 8px;
						height: 8px;
						background: $color-accent;
					}
				}
				&.slick-active button {
					width: 8px;
					height: 8px;
					background: $color-accent;
					transition: initial;
				}
			}
		}

		&--nav {
			@include respond-to(xs) {
				display: none;
			}
			.slick-slide {
				margin: 0 3px;
				cursor: pointer;
				outline: none;
				position: relative;
				&::after {
					content: "";
					display: none;
					width: 100%;
					height: 2px;
					background-color: $color-accent;
				}
				&.slick-current {
					&::after {
						display: block;
					}
				}
			}
		}
	} //.card__slider

	&__desc {
		width: 60%;

		@include respond-to(sm) {
			width: 50%;
		}

		@include respond-to(xs) {
			width: 100%;
		}
	} //.card__desc

	&__title {
		display: block;
		margin: $gutter * 1.5 0;
		font-size: 30px;

		@include respond-to(sm) {
			font-size: 28px;
		}

		@include respond-to(xs) {
			margin: $gutter/2 0;
			font-size: 23px;
			text-align: left;
		}
	} //.card__title

	&__item-select {
		margin: $gutter * 1.5 0;

		@include respond-to(xs) {
			max-width: 100%;
			margin: 0 0 $gutter * 1.5;
		}
	} //.card__item-select

	&__price {
		margin-bottom: $gutter * 1.5;
		font-family: $f2-bold;
		font-size: 23px;
		@include respond-to(xs) {
			margin-bottom: $gutter/1.5;
		}
		span {
			font-family: $f-light;
			font-size: 15px;
		}
		small {
			margin-left: 5px;
			font-size: 15px;
			color: $color-danger;
		}
	} //.card__price

	&__btn {
		font-size: 16px;
		position: relative;

		&--back {
			margin-bottom: 0;
			margin-left: auto;
			padding-left: 15px;
			font-family: $f-light;
			font-size: 14px;
			color: $color-text;

			@include respond-to(xs) {
				display: none;
			}

			&::before,
			&::after {
				content: "";
				position: absolute;
				top: 50%;
			}

			&::before {
				border: 1px solid #b7b7b8;
				border-left: 0 none;
				border-radius: 0 5px 5px 0;
				background-clip: padding-box;
				height: 8px;
				left: 0;
				margin: -4px 0 0;
				width: 10px;
			}
			&::after {
				transform: rotate(45deg);
				border-bottom: 1px solid #b7b7b8;
				border-left: 1px solid #b7b7b8;
				height: 5px;
				left: 1px;
				margin: -6px 0 0;
				width: 5px;
			}

			&.button--accent {
			}

			&.button--accent {
				display: inline-block;
				margin: 15px auto;
				color: $color-accent;

				@include respond-to(xs) {
					margin: $card-indent/2 auto 0;
				}

				&::before,
				&::after {
					margin-left: 15px;
					border-color: $color-accent;
					@include tr;
				}
				&:hover {
					&::before,
					&::after {
						border-color: #fff;
					}
				}
			}
		} //.card__btn--back

		&--cart {
			padding-right: 60px;
			padding-left: 20px;
			@include btnBig;

			span {
				overflow: hidden;
				background-color: $gray--darken;
				border-radius: 0 $border-radius $border-radius 0;
				position: absolute;
				top: 0;
				right: 0;
				display: block;
				width: 45px;
				height: 45px;

				&::after,
				&::before {
					font-family: fal;
					font-size: 18px;
					color: #fff;
					@include vy;
					@include tr();
				}

				&:first-child {
					&::after {
						content: "\f07a";
						right: 13px;
					}
					&::before {
						content: "\f218";
						left: -100px;
					}
				}
				&:last-child {
					display: none;
					&::after {
						content: "\f2ed";
						right: 14px;
					}
				}
			}
			&:hover {
				span {
					&:first-child {
						&::after {
							right: -100px;
						}
						&::before {
							left: 12px;
						}
					}
				}
			}
			&.is-checked {
				color: #fff;
				background-color: $color-main;
				span {
					&:first-child {
						display: none;
					}
					&:last-child {
						display: block;
					}
				}
			}
		} //.card__btn--cart

		&--fav {
			width: 45px;
			height: 45px;
			border: 1px solid $color-border--light;
			border-radius: 50%;
			&::before {
				font-size: 21px;
			}
		} //.card__btn--fav
	} //.card__btn

	&__back {
		display: block;
		width: 100%;
		padding: 5px 0;
		text-transform: uppercase;
		font-family: $f2-bold;
		font-size: 16px;
		text-align: center;
		position: relative;
		&::before {
			content: "\f104";
			font-family: far;
			font-size: 20px;
			@include vy;
			left: 0;
		}
		@include min-screen(481) {
			display: none;
		}
	} //.card__back

	.button-box {
		margin-bottom: $gutter * 1.5;
		@extend %flexCenter;
		flex-wrap: wrap;

		.button--yellow {
			@include respond-to(xs) {
				width: 85%;
			}
			@include respond-to(i7) {
				width: 83%;
			}
			@include respond-to(i5) {
				width: 80%;
			}
		}

		div {
			width: 45px;
			height: 45px;
			margin-left: 30px;

			@include respond-to(xs) {
				margin-left: 15px;
			}
		}
	}

	&__order-info {
		width: 100%;
		margin-top: 5px;
		font-size: 13px;
		span {
			font-family: $f2-bold;
		}
	} //.card__order-info

	&__info {
		max-width: 375px;
		@include respond-to(xs) {
			max-width: 100%;
		}
		.tab {
			&__titles {
				display: flex;
			}
		}
	} //.card__info

	&__related,
	&__colors {
		margin-bottom: $card-indent;

		@include respond-to(xs) {
			margin-bottom: $card-indent/1.5;
		}
	}

	&__related {
		.tab {
			&__titles {
				padding-bottom: 1px;
				overflow-x: auto;
			}

			&__title {
				padding-bottom: 15px;
				&:hover {
					.tab__text {
						color: $color-accent;
					}
				}
				&.ui-tabs-active {
					.tab__text {
						color: $color-accent;
					}
				}
			}

			&__contentes {
				padding: 35px;
			}

			&__link {
				display: inline-flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
			}
		}

		.bz-slider {
			&__arrow {
				height: 27px;
				line-height: 1;
				top: 50px;

				@include respond-to(md) {
					top: 58px;
				}

				@include respond-to(sm) {
					top: 47px;
				}

				@include respond-to(xs) {
					top: 36px;
				}

				@include respond-to(i7) {
					top: 51px;
				}

				@include respond-to(i5) {
					top: 37px;
				}

				&--prev {
					left: -30px;
				}

				&--next {
					right: -30px;
				}
			}
		}
	} //.card__related

	&__colors {
		@include respond-to(xs) {
			margin-bottom: 0;
		}
	} //.card__colors

	.accordeon {
		&__item {
			border-bottom: 1px solid $color-border;

			&:first-child {
				border-top: 1px solid $color-border;
			}
		}

		&__title {
			padding: 12px 0 12px 10px;
			font-family: $f;
			text-transform: uppercase;
		}

		&__content {
			padding: 0 10px 10px;
			p {
				font-size: 15px;
			}
		}
	}
} //.card

.related {
	&__img {
		margin-bottom: 10px;
	} //.related__img

	&__title {
		display: block;
		font-family: $f2-bold;
		font-weight: 500;
		text-align: center;
		text-transform: uppercase;
		font-size: 12px;
		span {
			display: block;
		}
	} //.related__title

	&__price {
		display: block;
		text-align: center;
		font-size: 12px;
	} //.related__price

	.tab {
		&__title {
			&:not(:last-child) {
				.tab__icon_wrap {
					position: relative;
					&::after {
						content: "";
						display: block;
						width: 100%;
						height: 1px;
						background-color: $gray--dark;
						position: absolute;
						top: 30px;
						right: -58px;
					}
				}
			}
		}
	}
} //.related

.item-select {
	height: 36px;
	line-height: 34px;
	margin-top: 20px;
	border: 1px solid $color-border;
	border-radius: $border-radius;
	cursor: pointer;
	outline: none;

	@include respond-to(i7) {
		margin-top: 30px;
	}

	&:hover {
		.item-select {
			&__value {
				color: $color-text;
			}
		}
	}

	&.is-active {
		.item-select {
			&__value {
				&::after {
					content: "\f106";
				}
			}
			&__list {
				display: block;
			}
		}
	}

	&.is-error {
		.item-select__error {
			display: block;
		}
	}

	&.is-close {
		.item-select {
			&__control {
				&--minus {
					color: #fff;
					background-color: $color-accent;
					border-color: $color-accent;
					&::before {
						content: "\f00d";
					}
				}
			}
		}
	}

	&__error {
		display: none;
		font-size: 13px;
		line-height: 1;
		text-align: center;
		color: $gray;
		position: absolute;
		bottom: -17px;
		left: 0;
		right: 0;
	} //.item-select__error

	&__group {
		max-width: 375px;
		position: relative;
		@extend %flexCenter;
		@include respond-to(i7) {
			flex-wrap: wrap;
		}
		.item-select {
			width: 50%;
			@include respond-to(i7) {
				width: 100%;
				position: relative;
			}
			& + .item-select {
				margin-left: 10px;
				@include respond-to(i7) {
					margin-left: 0;
				}
			}
		}
	} //.item-select__group

	&__caption {
		font-family: $f;
		color: $gray--dark;
		font-size: 13px;
		position: absolute;
		top: -10px;
		@include respond-to(i7) {
			top: -30px;
		}
	} //.item-select__caption

	&__value {
		height: 100%;
		padding: 0 30px;
		position: relative;
		&::after {
			content: "\f107";
			font-family: fal;
			font-size: 18px;
			color: $gray;
			@include vy;
			right: 8px;
			@include tr;
		}
	} //.item-select__value

	&__input {
		height: 36px;
		padding: 0 10px;
		font-size: 14px;
		text-align: center;
		border-top: 1px solid $color-border;
		border-bottom: 1px solid $color-border;
		border-left-color: transparent;
		border-right-color: transparent;
		&:focus {
			font-size: 14px;
			border-left-color: transparent;
			border-right-color: transparent;
		}
	} //.item-select__input

	&__color {
		display: block;
		width: 15px;
		height: 15px;
		border-radius: 50%;
		@include vy;
		left: 8px;
	} //.item-select__color

	&__title {
		display: block;
		width: 100%;
		font-family: $f;
		font-size: 14px;
		@include text-overflow;

		&_wrap {
			@extend %flexCenter;
			flex: 1;
		}

		&_desc {
			font-family: $f;
			font-size: 13px;
		}
	} //.item-select__title

	&__subtitle {
		font-family: $f;
		font-size: 13px;

		&.stock {
			font-family: $f-light;
			color: #20a837;
		}

		&.ends {
			font-family: $f-light;
			// color: #f60;
			color: $color-danger;
		}
	} //.item-select__title

	&__list {
		display: none;
		max-height: 211px;
		background-color: #fff;
		border: 1px solid $color-border;
		border-radius: 0 0 $border-radius $border-radius;
		position: absolute;
		top: 99%;
		left: 0;
		width: 100%;
		z-index: z(default);
		@include bz-scroll;

		@include respond-to(i5) {
			left: -1px;
			width: auto;
			right: -1px;
		}
	} //.item-select__list

	&__item {
		padding: 0 15px 0 30px;
		border-bottom: 1px solid $color-border--light;
		position: relative;
		@extend %flexCenter;

		&:last-child {
			border-bottom: 0;
		}

		&:hover {
			color: $color-text;
			background-color: $color-bg--lighten;
		}

		&--header {
			cursor: default;
			&:hover {
				background-color: #fff;
			}
			.item-select {
				&__title {
					font-size: 14px;
					@include nosel;
				}
				&__subtitle {
					width: 170px;
					font-size: 14px;
				}
			}
		}

		.item-select {
			&__title {
				width: auto;
				flex: 1;

				&_wrap {
					.item-select__title {
						flex: initial;
					}
				}
			}
		}
	} //.item-select__item

	&__control {
		display: inline-block;
		width: 36px;
		height: 36px;
		text-align: center;
		border: 1px solid $color-border;
		position: absolute;
		top: 0;
		bottom: 0;
		&::before {
			font-family: fal;
		}
		&:hover {
			color: #fff;
			background-color: $color-accent;
			border-color: $color-accent;
		}
		&--minus {
			border-radius: $border-radius 0 0 $border-radius;
			left: 0;
			&::before {
				content: "\f068";
			}
		}
		&--plus {
			border-radius: 0 $border-radius $border-radius 0;
			right: 0;
			&::before {
				content: "\f067";
			}
		}
	} //.item-select__control

	&--count {
		border: 0;
		.item-select {
			&__list {
				@include respond-to(i5) {
					left: 0;
					right: 0;
					top: 97%;
				}
			}
			&__value {
				padding: 0 33px;
				text-align: center;
				&::after {
					right: 45px;
				}
				.item-select__title {
					padding: 0 10px;
					border-top: 1px solid $color-border;
					border-bottom: 1px solid $color-border;
				}
			}
			&__title {
				padding: 0 7px;
				font-family: $f;
				span {
					font-size: 12px;
				}
			}
			&__subtitle {
				width: 170px;
				font-family: $f2-bold;
				font-size: 14px;
				position: relative;

				@include respond-to(i5) {
					width: 130px;
				}

				span {
					font-size: 13px;
					color: $color-danger;
					position: relative;
					top: -8px;
				}
			}
			&__item {
				padding: 0 10px 0 2px;
				&--header {
					.item-select__subtitle {
						font-family: $f;
					}
				}
			}
		}
	} //.item-select--count
} //.item-select

.bz-table {
	&__list {
		@extend %flexColumn;
	} //bz-table__list

	&__item {
		margin-bottom: 4px;
		display: flex;
	} //.bz-table__item

	&__subitem {
		min-width: 285px;
		width: 215px;
		font-size: 15px;
		color: $color-text;
		@include text-overflow;

		@include respond-to(md) {
			min-width: 250px;
		}

		@include respond-to(sm) {
			min-width: 175px;
		}
		&:last-child {
			margin-right: 0;
		}

		span {
			font-family: $f-bold;
			margin-right: 5px;
			background-color: #fff;
		}
	} //.bz-table__subitem

	&--dot {
		.bz-table {
			&__subitem {
				&:first-child {
					background-image: url(../img/general/dot.png);
					background-position: 0px 14px;
					background-repeat: repeat-x;
				}
			}
		}
	} //.bz-table--dot

	&--column {
		.bz-table {
			&__list {
				flex-direction: row;
			}
			&__item {
				min-width: 215px;
				width: 215px;
				flex-direction: column;
			}
			&__subitem {
				&:first-child {
					margin-bottom: 10px;
				}
			}
		}
	} //.bz-table--column
} //.bz-table
